
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'

export default {
components: {
XHeader,
XImg,
Box,
XInput,
XButton,
Group,
Cell,
CellBox,
XSwitch
},
data () {
return {
showLoading: false,
foodSaName: '',
foodSaCard: '',
showCardpicA: false,
showCardpicB: false,
hasPicA: true,
hasPicB: true,
srcA: '',
srcB: ''
}
},
mounted () {
},
computed: {},
methods: {
conformSa: function () {
this.showLoading = true;

/* this.$http('').then((res) => {
console.log(res)
}) */
},
showCard: function (type) {
if (type === 'A') {
this.showCardpicA = !this.showCardpicA
} else {
this.showCardpicB = !this.showCardpicB
}
},
initSrc: function (event) {
let _this = this;
let file = event.currentTarget.files[0];
let reader = new FileReader();
reader.onloadend = function () {
if (event.target.name === 'cardA') {
_this.srcA = reader.result;
} else {
_this.srcB = reader.result;
}
};
if (file) {
reader.readAsDataURL(file);
}
},
addPic: function (card) {
let cardNode = document.querySelectorAll('input[name="' + card + '"]')[0];

var evt = new MouseEvent('click', {
bubbles: false,
cancelable: true,
view: window
});
cardNode.dispatchEvent(evt);
}
}
}



